.skillsContainer{
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}
.skillsItems{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
}
@media screen and (max-width: 360px){
    .skillsItems{
        flex-direction: column;
    }
}